export function Whitespot() {
    useEffect(() => {
        document.body.classList.add('custom-background');
        return () => {
            document.body.classList.remove('custom-background');
        };
    });

    const navigate = useNavigate();
    const backtohome = ()=>{
        navigate('/nailweb');
    };
    return (
        <div className='page_body' style={{color:'white'}}>
                <h1>:(white spot)</h1>
                <img src={whitespotpage} className='insidepic'></img>
                <p style={{fontSize:'30px', fontWeight:'bold'}}></p>
                <p style={{fontSize:'20px'}}>1....</p>
                <p style={{fontSize:'20px'}}>2.115</p>
                :<a href="https://www.edh.tw/article/17488"></a>
                <Button onClick={backtohome}  style={{display: 'block', margin: '0 auto' }}></Button>
        </div>
    );
};

